@font-face {
	font-family: 'simple-calendar';
	src: url('../fonts/simple-calendar.eot?43976014');
	src: url('../fonts/simple-calendar.eot?43976014#iefix') format('embedded-opentype'),
		url('../fonts/simple-calendar.woff?43976014') format('woff'),
		url('../fonts/simple-calendar.ttf?43976014') format('truetype'),
		url('../fonts/simple-calendar.svg?43976014#simple-calendar') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^='simcal-icon-']:before,
[class*=' simcal-icon-']:before {
	display: inline-block;
	font-family: 'simple-calendar';
	font-style: normal;
	font-weight: normal;
	margin-right: 0.2em;
	speak: none;
	text-decoration: inherit;
	text-align: center;
	width: 1em;
	font-variant: normal;
	text-transform: none;
	margin-left: 0.2em;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.simcal-icon-spin {
	-moz-animation: spin 2s infinite linear;
	-o-animation: spin 2s infinite linear;
	-webkit-animation: spin 2s infinite linear;
	animation: spin 2s infinite linear;
	display: inline-block;
}

@-moz-keyframes spin {
	0% {
		-moz-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-moz-transform: rotate(359deg);
		-o-transform: rotate(359deg);
		-webkit-transform: rotate(359deg);
		transform: rotate(359deg);
	}
}

@-webkit-keyframes spin {
	0% {
		-moz-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-moz-transform: rotate(359deg);
		-o-transform: rotate(359deg);
		-webkit-transform: rotate(359deg);
		transform: rotate(359deg);
	}
}

@-o-keyframes spin {
	0% {
		-moz-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-moz-transform: rotate(359deg);
		-o-transform: rotate(359deg);
		-webkit-transform: rotate(359deg);
		transform: rotate(359deg);
	}
}

@-ms-keyframes spin {
	0% {
		-moz-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-moz-transform: rotate(359deg);
		-o-transform: rotate(359deg);
		-webkit-transform: rotate(359deg);
		transform: rotate(359deg);
	}
}

@keyframes spin {
	0% {
		-moz-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-moz-transform: rotate(359deg);
		-o-transform: rotate(359deg);
		-webkit-transform: rotate(359deg);
		transform: rotate(359deg);
	}
}

.simcal-icon-animate:before {
	transition: all 0.2s ease-in-out;
}

.simcal-icon-rotate-180:before {
	transform: rotate(180deg);
