.simcal-box-handle {
	display: inline-block;
	line-height: 1;
	margin-left: 8px;
}
.simcal-box-handle:before {
	content: '\2014';
	display: inline-block;
	font-weight: normal;
}
.simcal-box-handle label {
	font-size: 12px;
	font-weight: normal;
	margin-right: 10px;
	vertical-align: baseline;
}
.simcal-box-handle select {
	font-weight: bold;
	margin: -3px 0 0 0.5em;
	vertical-align: middle;
}

.simcal-tabs {
	background: #fafafa;
	border-right: 1px solid #eee;
	box-sizing: border-box;
	float: left;
	line-height: 1em;
	margin: 0;
	padding: 0 0 10px;
	position: relative;
	width: 20%;
}
.simcal-tabs a {
	border-bottom: 1px solid #eee;
	display: block;
	line-height: 20px !important;
	margin: 0;
	padding: 10px;
	text-decoration: none;
}
.simcal-tabs a:focus {
	box-shadow: none;
}
.simcal-tabs li {
	display: block;
	margin: 0;
	padding: 0;
}
.simcal-tabs li.active a {
	background-color: #eee;
	color: #555;
	position: relative;
}
.simcal-tabs:after {
	background-color: #fafafa;
	border-right: 1px solid #eee;
	bottom: -9999em;
	content: '';
	display: block;
	height: 9999em;
	left: 0;
	position: absolute;
	width: 100%;
}

.simcal-panels-wrap {
	background: #fff;
	overflow: hidden;
}

.simcal-panel {
	margin-left: 20%;
