}
.simcal-panel > table {
	padding-bottom: 11px;
	width: 100%;
}
.simcal-panel > table thead th {
	border-bottom: 1px solid #eee;
	font-size: 14px;
	font-weight: bold;
	padding: 12px 0 15px 10px;
	text-align: left;
}
.simcal-panel > table > tbody tr:first-child td,
.simcal-panel > table > tbody tr:first-child th {
